import { useTranslation } from 'react-i18next'

import { Box } from '@mui/system'
import About from './about'

const SideImg = ({ infos }) => {
  return (
    <Box
      sx={{
        textAlign: 'center',
        color: 'black',
        width: '100%',
        overflow: 'hidden',
        borderRadius: '10px'
      }}
    >
      <img
        className='swiperGB'
        src={require(`@/Assets/Images/home/swiper1.jpeg`)}
        alt={'swiper1.jpeg'}
        loading='lazy'
      />
    </Box>
  )
}

const ItemDirection = ({ direction, infos, onClick }) => {
  if (direction) {
    return (
      <>
        <About onClick={onClick} infos={infos}></About>
        <SideImg infos={infos}></SideImg>
      </>
    )
  } else {
    return (
      <>
        <SideImg></SideImg>
        <About onClick={onClick} infos={infos}></About>
      </>
    )
  }
}

const ImgText = ({ direction, infos, children, onClick }) => {
  return (
    <div className='imgText'>
      <Box
        sx={{
          pt: 3,
          px: 2,
          display: 'flex',
          justifyContent: 'center',
          alignContent: 'center',
          width: '100%'
        }}
      >
        <ItemDirection
          onClick={onClick}
          infos={infos}
          direction={direction}
        ></ItemDirection>
        {children}
      </Box>
    </div>
  )
}

export default ImgText
