import { Routes, Route } from 'react-router-dom'
import routesList from '@/Router/routes-list.jsx'

function Router () {
  return (
    <Routes>
      {routesList.map(route => (
        <Route key={route.key} path={route.path} element={<route.element />} />
      ))}
    </Routes>
  )
}

export default Router
