import { useTranslation } from 'react-i18next'
import { useNavigate } from 'react-router-dom'
import { useState } from 'react'
import Typography from '@mui/material/Typography'
import './style.css'
import { Stack } from '@mui/material'
import Box from '@mui/material/Box'
import PageTitle from '@/Components/pageTitle'
import news from '@/Assets/mock/news'
import Pagination from '@mui/material/Pagination'
const NewsList = ({ page, onClick }) => {
  let list = news
  list = JSON.parse(JSON.stringify(list))
  const renderList = list.slice((page - 1) * 10, page * 10)
  return (
    <Box
      sx={{
        maxWidth: '1000px',
        flexWrap: 'wrap',
        display: 'flex',
        justifyContent: 'space-evenly'
      }}
    >
      {renderList.map((item, index) => {
        return (
          <Stack sx={{ py: 4, width: '45%', maxWidth: '450px' }} key={index}>
            <Typography
              onClick={() => {
                onClick(item.id)
              }}
              variant='h4'
              gutterBottom
              component='div'
            >
              {item.headings}
            </Typography>
            <Typography variant='body2' gutterBottom component='div'>
              subtitle1. Lorem ipsum dolor sit amet, consectetur adipisicing
              elit. Quos blanditiis tenetur
            </Typography>
          </Stack>
        )
      })}
    </Box>
  )
}

const IndustryInformation = () => {
  const [page, setPage] = useState(1)
  const navigate = useNavigate()

  const handlePageChange = (event, value) => {
    setPage(value)
  }

  const handleItemClick = value => {
    navigate('/news-detail', { replace: true, state: { value: value } })
  }

  const count = Math.ceil(news.length / 10)

  return (
    <Stack justifyContent='center' alignItems='center'>
      <PageTitle title=''></PageTitle>
      <NewsList onClick={handleItemClick} page={page}></NewsList>
      <Pagination
        sx={{
          py: 6
        }}
        count={count}
        page={page}
        onChange={handlePageChange}
        variant='outlined'
        shape='rounded'
      />
    </Stack>
  )
}

export default IndustryInformation
