import { useTranslation } from 'react-i18next'
import { useState, useEffect } from 'react'
import Typography from '@mui/material/Typography'
import './style.css'
import { Stack } from '@mui/material'
import Divider from '@mui/material/Divider'
import Box from '@mui/material/Box'
import GrayButton from '@/Components/grayButton'
import PageTitle from '@/Components/pageTitle'
import ImgText from '@/Components/about/ImgText'
import apartments from '@/Assets/mock/apartments'
import theme from '@/Assets/Styles/theme'
import Pagination from '@mui/material/Pagination'
import { useNavigate } from 'react-router-dom'

const listMap = {
  0: apartments.cbd,
  1: apartments.zone
}

const Switcher = ({ nav, onClick }) => {
  return (
    <Stack
      sx={{ pb: 3 }}
      direction='row'
      justifyContent='center'
      alignItems='center'
    >
      <GrayButton
        onClick={() => {
          onClick(0)
        }}
        hightLightColor={theme.themeColor}
        hightLight={!nav}
        label='CBD'
      ></GrayButton>
      <Divider variant='middle' orientation='vertical' flexItem></Divider>
      <GrayButton
        onClick={() => {
          onClick(1)
        }}
        hightLightColor={theme.themeColor}
        hightLight={nav}
        label='district'
      ></GrayButton>
    </Stack>
  )
}

const NavList = ({ zone, nav, onClick }) => {
  return (
    <Stack
      sx={{ pt: 3 }}
      direction='row'
      justifyContent='center'
      alignItems='center'
    >
      <Box sx={{ display: 'flex', flexWrap: 'wrap' }}>
        {listMap[nav].map((item, index) => {
          return (
            <GrayButton
              onClick={() => {
                onClick(index)
              }}
              label={item.name}
              key={item.name}
              hightLightColor={theme.themeColor}
              hightLight={index === zone}
            ></GrayButton>
          )
        })}
      </Box>
    </Stack>
  )
}

const ApartmentList = ({ zone, nav, page, onClick }) => {
  const navigate = useNavigate()

  let list = listMap[nav][zone].list
  list = JSON.parse(JSON.stringify(list))
  const renderList = list.slice((page - 1) * 10, page * 10)

  const handleOnClick = (event, value) => {
    navigate('/apartment-detail', { replace: true, state: { value: 1 } })
  }

  return (
    <Stack>
      {renderList.map((item, index) => {
        return (
          <ImgText onClick={handleOnClick} key={item} direction={0}></ImgText>
        )
      })}
    </Stack>
  )
}

const Apartments = () => {
  const [nav, setNav] = useState(0)
  const [page, setPage] = useState(1)
  const [zone, setZone] = useState(0)

  const handlePageChange = (event, value) => {
    setPage(value)
  }

  const count = Math.ceil(listMap[nav][zone].list.length / 10)

  return (
    <Stack justifyContent='center' alignItems='center'>
      <PageTitle title=''></PageTitle>
      <Switcher
        nav={nav}
        onClick={index => {
          setNav(index)
          setZone(0)
          setPage(1)
        }}
      ></Switcher>
      <Divider variant='middle' flexItem></Divider>
      <NavList
        onClick={index => {
          setZone(index)
          setPage(1)
        }}
        zone={zone}
        nav={nav}
      ></NavList>
      <ApartmentList zone={zone} nav={nav} page={page}></ApartmentList>
      <Pagination
        sx={{
          py: 6
        }}
        count={count}
        page={page}
        onChange={handlePageChange}
        variant='outlined'
        shape='rounded'
      />
    </Stack>
  )
}

export default Apartments
