import Button from '@mui/material/Button'

const GrayButton = ({ hightLightColor, label, onClick, hightLight }) => {
  let newColor
  if (hightLightColor) {
    newColor = hightLightColor
  } else {
    newColor = 'black'
  }

  return (
    <Button
      onClick={onClick}
      sx={{ color: hightLight ? newColor : 'gray', minWidth: 100 }}
    >
      {label}
    </Button>
  )
}
export default GrayButton
